canvas {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 0;
}

.ui {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
  pointer-events: none;
  width: 100%;
  height: 100%;
}

.ui button {
  padding: 1em;
  margin: 1em;
  margin-top: auto;
  margin-left: auto;
  pointer-events: auto;
}

